import React from 'react';
import { Cell, Div, Avatar, Link, } from '@vkontakte/vkui';
import { getRankByRating, formatCoins } from '../helpers';

const ExchangeBuyItem = ({ id, firstName, lastName, amountDC, photoURL, costOneDC, idVk, remaininAmoun }) => {
    const urlUser = 'https://vk.com/id' + idVk;
    const sumRUB = amountDC * costOneDC;


    return (
        <Div className='card'>
            <span className='listNumber'>{id}</span>
            <Link href={urlUser} target="_blank">
                <Cell
                    className='myItem'
                    before={photoURL ? <Avatar src={photoURL} /> :
                        <Avatar src={'https://vk.com/images/camera_200.png'} />}
                    description={
                        <Div className="div">
                            <Div style={{ padding: 2, margin: 0, display: 'flex', alignItems: 'start', flexDirection: 'row' }}>
                                <Div className="iconStar" />
                                <Div className="div"  style={{ whiteSpace: 'nowrap'}}>
                                    1 DC = {costOneDC} руб
                                </Div>
                            </Div>
                            <Div style={{ padding: 2, margin: 0, display: 'flex', alignItems: 'start', flexDirection: 'row' }}>
                                <Div>
                                    Всего {amountDC} 
                                </Div>
                                <Div className="iconDC" />
                                <Div >
                                     ({sumRUB} руб)
                                </Div>
                            </Div>
                            <Div style={{ padding: 2, margin: 0, display: 'flex', alignItems: 'start', flexDirection: 'row' }}>
                                <Div>
                                   Остаток: {remaininAmoun} DurkaCoin
                                </Div>
                                <Div className="iconDC" />
                            </Div>
                        </Div>
                    }
                // asideContent={<Icon24FavoriteOutline fill="black" />}
                // before={<Icon24FavoriteOutline/>}
                // indicator={rating}
                // indicator={<Icon24FavoriteOutline fill="black"/>}
                >
                    {firstName + " " + lastName}
                </Cell>
            </Link>
        </Div>
    );
};

export { ExchangeBuyItem };

